import React from 'react';
import Hero from '../components/Hero';
import {Helmet} from "react-helmet";
import ContentHome from '../components/ContentHome';

function HomePage(props) {

    return(
        <div>
            <Helmet>
                <title>Kuervo's Store ~ Figures and Collectibles</title>
            </Helmet>
            <section id="hero" class="hero">
                <div class="hero-contenido">
                    <Hero title={props.title} subTitle={props.subTitle} text={props.text} />
                </div>
            </section>

            <ContentHome>
                <h1 class="display-4 font-weight-light MT-4">BECOME THE ULTIMATE WEEB</h1>
                
                <p>Hello, my name is Danny. I'm a frontend jr developer with experience in Express JS, Node JS, MongoDB, React and more.</p>

                </ContentHome>
        </div>
        
    );
}

export default HomePage;